import { createBrowserRouter } from "react-router-dom";
import Root from "./routes/Root";
import VideoList from "./components/VideoList";
import BookList from "./components/BookList";

// 
/*
GET     /demo/video/list                
GET     /demo/video/search            

GET     /demo/book/list               ` 
GET     /demo/book/search            
GET     /demo/book/search /{:isbn}     

*/

const router = createBrowserRouter([
    {
        path:"/",
        element: <Root />,
        children : [
            {
                path : "/sample",
                element: <>
                    <p>.</p>
                </>
            }
        ],
        errorElement:<>
        <h1>Opps!!</h1>
        </>
    },{
        path:"/video",
        element : <Root />,
        children : [
            {
                path : "/video",
                element : <>
                        <VideoList />
                    </>
                
            },{
                path : "/video/search",
                element : <>
                        <VideoList />
                    </>
                
            },{
                path : "/video/list",
                element : <>
                        <VideoList />
                    </>
                
            }
        ]
    },
    {
        path:"/book",
        element : <Root />,
        children :[
            {
                path : "/book",
                element : <>
                        <BookList />
                    </>
                
            },{
                path : "/book/list",
                element : <>
                        <BookList />
                    </>
                
            },{
                path : "/book/search",
                element : <>
                        <BookList />
                    </>
                
            },{
                path : "/book/search/:isbn",
                element : <>
                        <BookList />
                    </>
                
            }
        ]
    },
], {
    basename : "/demo"
});

export default router;